import { FC } from "react";
import classNames from "classnames/bind";

import { MapStatic } from "common/components/MapStatic/MapStatic";
import { Icon } from "common/components/Icons";
import CalendarIcon from "common/components/Icons/icons/24/calendar.svg";
import PinIcon from "common/components/Icons/icons/24/pin.svg";
import RightIcon from "common/components/Icons/icons/24/right.svg";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Text, TextTag } from "common/components/Text";
import { Link } from "common/components/Link";
import { getCalendarUrl, getCalendarDate } from "common/utils/googleCalendar";
import { getTime } from "common/utils/date";

import { AppointmentDetailsProps } from "./AppointmentDetails.types";
import styles from "./AppointmentDetails.module.scss";
import content from "./content";

const cx = classNames.bind(styles);
const MAP_DESKTOP_SIZE = 144;

export const AppointmentDetails: FC<AppointmentDetailsProps> = ({
    language,
    title,
    booking,
    user,
    priceBlock,
    addressLabel,
    mapLabel,
    dateLabel,
    timeLabel,
    isMobile,
    className,
}) => {
    const calendar = content[language].calendar;
    const { location } = booking;

    const { eventStartDate, eventEndDate, eventStartDateFormatted } = booking;
    const startTime = getTime(eventStartDate);
    const endTime = getTime(eventEndDate);

    const googleCalendarDate = getCalendarDate(eventStartDate);

    const calendarLink =
        googleCalendarDate &&
        startTime &&
        endTime &&
        getCalendarUrl({
            title: calendar.title || "",
            startDate: `${googleCalendarDate}T${startTime.hours}${startTime.minutes}`,
            endDate: `${googleCalendarDate}T${endTime.hours}${endTime.minutes}`,
            // @TODO think how to store exact google map company
            location: `${calendar.organisationPrefix} ${user.name} ${location.address} ${location.city}`,
            details: calendar.description || "",
        });

    const eventTime =
        startTime &&
        endTime &&
        `${startTime.hours}:${startTime.minutes}—${endTime.hours}:${endTime.minutes}`;

    const renderMap = () => (
        <MapStatic
            className={cx("map")}
            zoom={15}
            coordinates={location.coordinates}
            width={isMobile ? window.innerWidth : MAP_DESKTOP_SIZE}
            height={
                isMobile
                    ? Math.round(window.innerWidth * 0.7)
                    : MAP_DESKTOP_SIZE
            }
            name={location.clinic.name}
        />
    );

    return (
        <div className={cx("root", className)}>
            <div className={cx("appointment")}>
                {title && (
                    <Heading
                        className={cx("title")}
                        type={HeadingType.M}
                        tag={HeadingTag.H2}
                    >
                        {title}
                    </Heading>
                )}

                {(eventStartDate || eventTime) && (
                    <div className={cx("appointment-info")}>
                        {eventStartDateFormatted && (
                            <div className={cx("appointment-item")}>
                                <span className={cx("appointment-item__title")}>
                                    {dateLabel}
                                </span>
                                <Heading
                                    tag={HeadingTag.H3}
                                    type={HeadingType.S}
                                    className={cx("event-date")}
                                >
                                    {eventStartDateFormatted}
                                </Heading>
                            </div>
                        )}
                        {eventTime && (
                            <div className={cx("appointment-item")}>
                                <span className={cx("appointment-item__title")}>
                                    {timeLabel}
                                </span>
                                <Heading
                                    tag={HeadingTag.H3}
                                    type={HeadingType.S}
                                    className={cx("event-time")}
                                >
                                    {eventTime}
                                </Heading>
                            </div>
                        )}
                        {priceBlock && (
                            <div
                                className={cx(
                                    "appointment-item",
                                    "appointment-item_price"
                                )}
                            >
                                <span className={cx("appointment-item__title")}>
                                    {priceBlock.label}
                                </span>
                                <Heading
                                    tag={HeadingTag.H3}
                                    type={HeadingType.S}
                                    className={cx("event-price")}
                                >
                                    {priceBlock.value}
                                </Heading>
                            </div>
                        )}
                    </div>
                )}
                {calendarLink && (
                    <Link
                        link={calendarLink}
                        className={cx("calendar")}
                        target="_blank"
                    >
                        <Icon className={cx("calendar__icon")}>
                            <CalendarIcon />
                        </Icon>
                        <Text className={cx("calendar__title")}>
                            {calendar?.label}
                        </Text>
                        <Icon className={cx("right-icon")}>
                            <RightIcon />
                        </Icon>
                    </Link>
                )}
            </div>

            <div className={cx("address")}>
                <div className={cx("address__column", "address__column_label")}>
                    <Text className={cx("address__title")} tag={TextTag.Span}>
                        {addressLabel}
                    </Text>
                </div>

                <div className={cx("address__column", "address__column_map")}>
                    {location.clinic.link ? (
                        <Link link={location.clinic.link} target="_blank">
                            {renderMap()}
                        </Link>
                    ) : (
                        renderMap()
                    )}
                </div>

                <div
                    className={cx("address__column", "address__column_address")}
                >
                    {(location.clinic.name || location.address) && (
                        <Heading
                            className={cx("address__location")}
                            tag={HeadingTag.H5}
                            type={HeadingType.S}
                        >
                            {location.clinic.name && (
                                <>{location.clinic.name} •&nbsp;</>
                            )}
                            {location.address}
                        </Heading>
                    )}
                </div>

                <div className={cx("address__column", "address__column_link")}>
                    {location.clinic.link && (
                        <Link
                            className={cx("link")}
                            link={location.clinic.link}
                            target="_blank"
                        >
                            <Icon className={cx("pin-icon")}>
                                <PinIcon />
                            </Icon>
                            <Text className={cx("link__title")}>
                                {mapLabel}
                            </Text>
                            <Icon className={cx("right-icon")}>
                                <RightIcon />
                            </Icon>
                        </Link>
                    )}
                </div>
            </div>
        </div>
    );
};
