import { FC, useEffect, useState } from "react";
import classNames from "classnames/bind";
import { useRouter } from "next/router";
import { ConfirmationStatus } from "@smile2impress/web-types";

import { Icon } from "common/components/Icons";
import LogoIcon from "common/components/Icons/icons/logo.svg";
import FreeFirstVisitESIcon from "common/components/Icons/icons/free-first-visit-es.svg";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Picture } from "common/components/Picture";
import { Video } from "common/components/Video";
import { CountryVersion, WINDOW_SIZE } from "common/constants/enum";
import { Container } from "common/components/Container";
import { Link } from "common/components/Link";
import { Text } from "common/components/Text";
import { Button, ButtonSize, ButtonTheme } from "common/components/new/Button";
import { getAnalyticsId } from "common/utils/AnalyticsId";
import {
    getShowUpCustomerBookings,
    putVisitIntentionStatus,
    cancelAllAppointments,
} from "common/api/contacts";
import { checkIfDateInFuture } from "common/utils/date";
import DoneIcon from "common/components/Icons/icons/24/done.svg";
import {
    AppointmentDetails,
    AppointmentDetailsBooking,
} from "common/components/AppointmentDetails";
import { normalizeBookingInfo } from "common/components/AppointmentDetails/helpers";

import { sortBookingsByDateAsc } from "./helpers";
import { RadioCard } from "./components/RadioCard";
import { CancelAllModal } from "./components";
import { CancelAllSuccessModal } from "./components";
import {
    ShowUpMainBannerProps,
    ShowUpUserInfo,
} from "./ShowUpMainBanner.types";
import styles from "./ShowUpMainBanner.module.scss";

const cx = classNames.bind(styles);

const COUNT_TICKER_WORDS_MOBILE = 10;

export const ShowUpMainBanner: FC<ShowUpMainBannerProps> = ({
    language,
    version,
    titlePrefix,
    title,
    titleConfirm,
    listSubtitle,
    listTitle,
    listDescription,
    listActiveBookings,
    confirmListButtonText,
    cancelAllButtonText,
    cancelAllModalContent,
    cancelAllSuccessModalContent,
    priceBlock,
    ticker,
    linkVideo,
    linkGif,
    linkLogo,
    dateLabel,
    timeLabel,
    addressLabel,
    mapLabel,
    confirmButtonText,
}) => {
    const router = useRouter();
    const { contact } = router?.query ?? {};

    const [isMobile, setIsMobile] = useState(false);
    const [userInfo, setUserInfo] = useState<ShowUpUserInfo | null>(null);
    const [bookingsInfo, setBookingsInfo] = useState<
        AppointmentDetailsBooking[]
    >([]);
    const [isError, setIsError] = useState<boolean>(false);
    const [isSelectedInFuture, setIsSelectedInFuture] = useState<boolean>(
        false
    );
    const [selectedBookingId, setSelectedBookingId] = useState<string>("");
    const [isCancelAllSuccess, setIsCancelAllSuccess] = useState<boolean>(
        false
    );
    const [isCancelAllLoading, setIsCancelAllLoading] = useState<boolean>(
        false
    );

    const fetchContact = async (uuid: string) => {
        const { data } = await getShowUpCustomerBookings(uuid);

        if (data && data?.bookings?.length) {
            const { bookings } = data;
            const sortedBookings = sortBookingsByDateAsc(bookings);
            const normalizedBookings = sortedBookings.map((booking) =>
                normalizeBookingInfo(booking, language)
            );

            setBookingsInfo(normalizedBookings);
        } else {
            setIsError(true);
        }

        setUserInfo({
            name: data?.firstName,
        });
    };

    useEffect(() => {
        const confirmedBooking = (bookingsInfo as AppointmentDetailsBooking[]).find(
            ({ status }) => status === ConfirmationStatus.Confirmed
        );

        if (bookingsInfo.length > 0) {
            setSelectedBookingId(bookingsInfo[0].id);
        }

        if (confirmedBooking) {
            setUserInfo({
                confirmedBooking: confirmedBooking || null,
            });
        }
    }, [bookingsInfo]);

    useEffect(() => {
        setIsMobile(window.innerWidth < WINDOW_SIZE.DESKTOP);
    }, [typeof window !== "undefined" && window.innerWidth]);

    useEffect(() => {
        if (contact) {
            fetchContact(contact as string);
        }
    }, [contact]);

    useEffect(() => {
        const selectedBooking = (bookingsInfo as AppointmentDetailsBooking[]).find(
            ({ id }) => id === selectedBookingId
        );
        const isSelectedInFuture = checkIfDateInFuture(
            selectedBooking?.eventStartDate
        );
        setIsSelectedInFuture(Boolean(isSelectedInFuture));
    }, [selectedBookingId]);

    const onConfirmationButtonClick = async () => {
        if (selectedBookingId) {
            const selectedBooking = bookingsInfo.find(
                (booking) => booking.id === selectedBookingId
            );

            const { data } = await putVisitIntentionStatus(
                contact as string,
                selectedBookingId
            );

            if (data && userInfo && selectedBooking) {
                setUserInfo({
                    confirmedBooking: {
                        ...selectedBooking,
                        status: data.visitIntentionStatus,
                    },
                });
            }
        }
    };

    const onCancelAllAppointments = async () => {
        setIsCancelAllLoading(true);

        try {
            await cancelAllAppointments(contact as string);
            setIsCancelAllLoading(false);
            setIsCancelAllSuccess(true);
        } catch (e) {
            setIsCancelAllLoading(false);
        }
    };

    const handleChangeRadioCard = (bookingId: string) => {
        setSelectedBookingId(bookingId);
    };

    const confirmationButtonId = getAnalyticsId({
        zone: "show-up-confirm",
    });

    const isConfirmed =
        userInfo?.confirmedBooking?.status === ConfirmationStatus.Confirmed;

    const hasBookingList = bookingsInfo.length > 1 && !isConfirmed;
    const hasOnlyOneBooking = bookingsInfo.length == 1;

    const cancellAllButtonContent = (
        <CancelAllModal
            control={(openModal) => (
                <Button
                    className={cx("actions__button")}
                    size={ButtonSize.M}
                    isMaxWidth={isMobile}
                    theme={ButtonTheme.BORDERED_TRANSPARENT}
                    onClick={openModal}
                >
                    {cancelAllButtonText}
                </Button>
            )}
            onConfirm={onCancelAllAppointments}
            isLoading={isCancelAllLoading}
            content={cancelAllModalContent}
            isMobile={isMobile}
        />
    );

    const bookingsListContent = (
        <div className={cx("bookings")}>
            <Heading
                className={cx("bookings-title")}
                tag={HeadingTag.H2}
                type={HeadingType.M}
            >
                {listActiveBookings}
            </Heading>

            <RadioCard
                className={cx("radio-card")}
                defaultValue={bookingsInfo[0]?.id}
                onChange={({ target }) => handleChangeRadioCard(target.value)}
                data={bookingsInfo.map(
                    ({
                        id,
                        eventStartDateFormatted,
                        eventStartTime,
                        location,
                    }) => ({
                        id,
                        date: eventStartDateFormatted,
                        time: eventStartTime,
                        location,
                        value: id,
                    })
                )}
            />

            <div
                className={cx("actions", {
                    "actions_has-list": hasBookingList,
                })}
            >
                <Button
                    className={cx("actions__button")}
                    size={ButtonSize.M}
                    id={confirmationButtonId}
                    isMaxWidth={isMobile}
                    onClick={onConfirmationButtonClick}
                    {...(isConfirmed && {
                        theme: ButtonTheme.BORDERED_TRANSPARENT,
                        icon: <DoneIcon />,
                    })}
                    disabled={!isSelectedInFuture || isConfirmed}
                >
                    {confirmListButtonText}
                </Button>

                {!isCancelAllSuccess && cancellAllButtonContent}
            </div>
        </div>
    );

    const renderIcon = () => {
        switch (version) {
            case CountryVersion.SPAIN:
                return <FreeFirstVisitESIcon />;
            default:
                return null;
        }
    };

    return (
        <div
            className={cx("root", {
                "root_has-list": hasBookingList,
            })}
        >
            <div
                className={cx("header", {
                    "header_has-list": hasBookingList,
                })}
            >
                <Container className={cx("container")}>
                    <Link link={linkLogo}>
                        <Icon className={cx("logo")}>
                            <LogoIcon />
                        </Icon>
                    </Link>
                </Container>
            </div>

            <Container
                className={cx("main-container", {
                    "main-container_has-list": hasBookingList,
                })}
            >
                <div className={cx("column", "column_text")}>
                    {!isConfirmed && !hasBookingList && (
                        <>
                            <Heading
                                className={cx("subtitle")}
                                tag={HeadingTag.H4}
                                type={HeadingType.S}
                            >
                                {titlePrefix}
                                {userInfo?.name ? ` ${userInfo?.name}!` : "!"}
                            </Heading>
                            <Heading
                                className={cx("title")}
                                tag={HeadingTag.H1}
                            >
                                {title}
                            </Heading>
                        </>
                    )}

                    {isConfirmed && (
                        <Heading className={cx("title")} tag={HeadingTag.H1}>
                            {titleConfirm}
                        </Heading>
                    )}

                    {!isConfirmed && hasBookingList && (
                        <>
                            <Heading
                                className={cx("subtitle")}
                                tag={HeadingTag.H4}
                                type={HeadingType.S}
                            >
                                {listSubtitle}
                            </Heading>
                            <Heading
                                className={cx("list-title")}
                                tag={HeadingTag.H1}
                            >
                                {listTitle}
                            </Heading>
                            <Text className={cx("description")}>
                                {listDescription}
                            </Text>
                        </>
                    )}

                    {(hasOnlyOneBooking || isConfirmed) &&
                        contact &&
                        !isError && (
                            <AppointmentDetails
                                version={version}
                                language={language}
                                className={cx("appointment-details")}
                                booking={
                                    userInfo?.confirmedBooking ||
                                    bookingsInfo[0]
                                }
                                user={{ name: userInfo?.name }}
                                dateLabel={dateLabel}
                                timeLabel={timeLabel}
                                addressLabel={addressLabel}
                                mapLabel={mapLabel}
                                priceBlock={priceBlock}
                                isMobile={isMobile}
                            />
                        )}

                    {hasOnlyOneBooking && contact && (
                        <div className={cx("actions")}>
                            <Button
                                className={cx("actions__button")}
                                size={ButtonSize.M}
                                id={confirmationButtonId}
                                onClick={onConfirmationButtonClick}
                                {...(isConfirmed && {
                                    theme: ButtonTheme.BORDERED_TRANSPARENT,
                                    icon: <DoneIcon />,
                                })}
                                disabled={isConfirmed}
                                isMaxWidth={isMobile}
                            >
                                {confirmButtonText}
                            </Button>

                            {!isConfirmed &&
                                !isCancelAllSuccess &&
                                cancellAllButtonContent}
                        </div>
                    )}
                </div>

                {isMobile && hasBookingList && (
                    <div className={cx("mobile-booking-list")}>
                        {bookingsListContent}
                    </div>
                )}

                <CancelAllSuccessModal
                    content={cancelAllSuccessModalContent}
                    isVisible={isCancelAllSuccess}
                    isMobile={isMobile}
                />

                <div
                    className={cx("column", "column_video", {
                        "column_has-list": hasBookingList,
                    })}
                >
                    <Video
                        className={cx("video")}
                        width="100%"
                        height="auto"
                        autoPlay={true}
                        muted={true}
                        controls={true}
                        src={linkVideo}
                    />
                    {version === CountryVersion.SPAIN && (
                        <div className={cx("first-visit-text")}>
                            <Icon>{renderIcon()}</Icon>
                        </div>
                    )}

                    <Picture
                        mobile={{
                            gif: linkGif,
                            media: `(max-width: ${WINDOW_SIZE.DESKTOP}px)`,
                        }}
                        classNameImg={cx("image")}
                        alt="clinic demo"
                    />
                </div>
            </Container>

            {!isMobile && hasBookingList && (
                <div className={cx("desktop-booking-list")}>
                    {bookingsListContent}
                </div>
            )}

            <div className={cx("ticker-wrap")}>
                <ul className={cx("ticker")}>
                    {new Array(COUNT_TICKER_WORDS_MOBILE)
                        .fill(ticker)
                        .map((text, index) => {
                            return (
                                <li key={index} className={cx("ticker-item")}>
                                    {text}
                                </li>
                            );
                        })}
                </ul>
            </div>
        </div>
    );
};
