import React, { FC } from "react";
import classNames from "classnames/bind";
import Carousel, { ArrowProps, ResponsiveType } from "react-multi-carousel";

import { Picture } from "common/components/Picture";
import { WINDOW_SIZE } from "common/constants/enum";
import { Icon } from "common/components/Icons";
import { Container } from "common/components/Container";
import ArrowLeftIcon from "common/components/Icons/icons/24/arrow-left.svg";
import { Heading, HeadingTag } from "common/components/new/Heading";

import { SliderProps, DirectionArrow } from "./Slider.types";
import styles from "./Slider.module.scss";

const cx = classNames.bind(styles);

const responsive: ResponsiveType = {
    desktop: {
        breakpoint: {
            min: WINDOW_SIZE.DESKTOP,
            max: 3000,
        },
        items: 1,
    },
    mobile: {
        breakpoint: {
            min: 0,
            max: WINDOW_SIZE.DESKTOP - 1,
        },
        items: 1,
    },
};

const Arrow: FC<ArrowProps & { direction: DirectionArrow }> = ({
    onClick,
    direction,
}) => {
    return (
        <Container className={cx("container-arrow")}>
            <button
                onClick={onClick}
                className={cx("arrow", {
                    "arrow-right": direction === DirectionArrow.RIGHT,
                    "arrow-left": direction === DirectionArrow.LEFT,
                })}
                type="button"
            >
                <Icon
                    className={cx("arrow-icon", {
                        "arrow-icon-right": direction === DirectionArrow.RIGHT,
                        "arrow-icon-left": direction === DirectionArrow.LEFT,
                    })}
                >
                    <ArrowLeftIcon />
                </Icon>
            </button>
        </Container>
    );
};

export const Slider: FC<SliderProps> = ({
    images,
    title,
    titleTag = HeadingTag.H2,
    className,
}) => {
    return (
        <section className={cx("root", className)}>
            {title && (
                <Container>
                    <Heading className={cx("title")} tag={titleTag}>
                        {title}
                    </Heading>
                </Container>
            )}
            <Carousel
                className={cx("list")}
                sliderClass={cx("container")}
                itemClass={cx("item")}
                responsive={responsive}
                removeArrowOnDeviceType={"mobile"}
                customLeftArrow={<Arrow direction={DirectionArrow.LEFT} />}
                customRightArrow={<Arrow direction={DirectionArrow.RIGHT} />}
                infinite
                centerMode={true}
            >
                {images.map((image, index) => {
                    return (
                        <Picture
                            {...image}
                            key={index}
                            classNameImg={cx("image")}
                        />
                    );
                })}
            </Carousel>
        </section>
    );
};
