export enum MapStaticImageFormats {
    PNG = "png",
    JPEG = "jpg",
}

export interface ClinicCoordinates {
    lat: number;
    lng: number;
}

export type MapStaticType = {
    zoom: number;
    coordinates: ClinicCoordinates;
    width: number;
    height: number;

    className?: string;
    name?: string;
    scale?: number;
    format?: MapStaticImageFormats;
    showBaseMarker?: boolean;
};
