export default function getMapStringStyles(styles: google.maps.MapTypeStyle[]) {
    const result: string[] = [];

    styles.forEach((v) => {
        let style = "";
        if (v.stylers && v.stylers.length > 0) {
            style +=
                (v.hasOwnProperty("featureType")
                    ? `feature:${v.featureType}`
                    : "feature:all") + "|";
            style +=
                (v.hasOwnProperty("elementType")
                    ? `element:${v.elementType}`
                    : "element:all") + "|";
            v.stylers.forEach((val: { [key: string]: any }) => {
                const propertyname = Object.keys(val)[0];
                const propertyval = val[propertyname]
                    .toString()
                    .replace("#", "0x");
                style += `${propertyname}:${propertyval}|`;
            });
        }
        result.push(`style=${encodeURIComponent(style)}`);
    });

    return result.join("&");
}
