import { FC } from "react";
import classNames from "classnames/bind";

import { RadioGroup } from "common/components/new/Radio";
import { Text } from "common/components/Text";
import { Icon } from "common/components/Icons";
import PipeIcon from "common/components/Icons/icons/24/pipe.svg";
import PinIcon from "common/components/Icons/icons/24/pin.svg";

import { RadioCardProps } from "./RadioCard.types";
import styles from "./RadioCard.module.scss";

const cx = classNames.bind(styles);

export const RadioCard: FC<RadioCardProps> = ({
    data,
    defaultValue,
    onChange,
    className,
}) => (
    <div className={cx(className, "root")}>
        <RadioGroup defaultValue={defaultValue} onChange={onChange}>
            {data.map(({ id, value, date, time, location }) => (
                <RadioGroup.RadioWithLabel
                    key={id}
                    id={id}
                    value={value}
                    className={cx("radio-card")}
                >
                    <div className={cx("content")}>
                        <div className={cx("date-content")}>
                            <Text className={cx("date")}>{date}</Text>
                            <Icon className={cx("pipe-icon")}>
                                <PipeIcon />
                            </Icon>
                            <Text className={cx("time")}>{time}</Text>
                        </div>
                        <div className={cx("address-content")}>
                            <Icon className={cx("pin-icon")}>
                                <PinIcon />
                            </Icon>
                            <Text className={cx("address")}>
                                {location.address}
                            </Text>
                        </div>
                    </div>
                </RadioGroup.RadioWithLabel>
            ))}
        </RadioGroup>
    </div>
);
