import React, { FunctionComponent, useEffect } from "react";
import classnames from "classnames/bind";

import { Modal, ModalPosition } from "common/components/Modal";
import { Button, ButtonSize, ButtonTheme } from "common/components/new/Button";
import { Heading, HeadingTag } from "common/components/new/Heading";
import LogoIcon from "common/components/Icons/icons/logo.svg";
import CancelIcon from "common/components/Icons/icons/48/cancel.svg";
import { SocialMediaIcon } from "common/components/Icons/SocialMediaIcon";
import { Icon } from "common/components/Icons";
import { Link } from "common/components/Link";
import { Text } from "common/components/Text";

import { CancelAllSuccessModalProps } from "./CancelAllSuccessModal.types";
import styles from "./CancelAllSuccessModal.module.scss";

const cx = classnames.bind(styles);

export const CancelAllSuccessModal: FunctionComponent<CancelAllSuccessModalProps> = ({
    content,
    isVisible,
    isMobile,
}) => {
    const { label, bookNewVisitButtonText, contacts, buttonLink } = content;

    useEffect(() => {
        if (isVisible) {
            document.body.style.overflow = "hidden";
        } else {
            document.body.style.overflow = "auto";
        }
    }, [isVisible]);

    return (
        <>
            {isVisible && (
                <Modal
                    onClose={() => {}}
                    showCloseButton={false}
                    position={ModalPosition.FULL}
                    classNameBody={cx("body")}
                >
                    <div className={cx("content")}>
                        <Icon className={cx("logo")}>
                            <LogoIcon />
                        </Icon>
                        <Icon className={cx("label-icon")}>
                            <CancelIcon />
                        </Icon>
                        <Heading className={cx("label")} tag={HeadingTag.H2}>
                            {label}
                        </Heading>
                        <Button
                            className={cx("button")}
                            size={ButtonSize.M}
                            theme={ButtonTheme.BORDERED_TRANSPARENT}
                            isMaxWidth={isMobile}
                            link={buttonLink}
                        >
                            {bookNewVisitButtonText}
                        </Button>
                        <ul className={cx("contacts-list")}>
                            {contacts.map(({ name, link, icon }) => (
                                <li
                                    className={cx("contacts-list__item")}
                                    key={name}
                                >
                                    <Link
                                        className={cx("contacts-list__link")}
                                        link={link}
                                    >
                                        <Text
                                            className={cx(
                                                "contacts-list__text"
                                            )}
                                        >
                                            {name}
                                        </Text>
                                        <Icon
                                            className={cx(
                                                "contacts-list__icon"
                                            )}
                                        >
                                            <SocialMediaIcon iconName={icon} />
                                        </Icon>
                                    </Link>
                                </li>
                            ))}
                        </ul>
                    </div>
                </Modal>
            )}
        </>
    );
};
