import React, { FC } from "react";
import classNames from "classnames/bind";

import googleMapStyles from "common/utils/googleMapStyles";
import getMapStringStyles from "common/utils/getMapStringStyles";
import { Icon } from "common/components/Icons";
import PinIcon from "common/components/Icons/icons/pin.svg";

import { MapStaticType, MapStaticImageFormats } from "./MapStatic.types";
import styles from "./MapStatic.module.scss";

const GOOGLE_STATIC_MAP_API_KEY = process.env.GOOGLE_STATIC_MAP_API_KEY;
const GOOGLE_MAP_API_URL = `https://maps.googleapis.com/maps/api/staticmap?key=${GOOGLE_STATIC_MAP_API_KEY}`;

const cx = classNames.bind(styles);

export const MapStatic: FC<MapStaticType> = ({
    className,
    zoom,
    scale = 2,
    format = MapStaticImageFormats.JPEG,
    coordinates,
    width,
    height,
    showBaseMarker = true,
    name,
}) => {
    const style = getMapStringStyles(googleMapStyles);
    const { lat, lng } = coordinates;

    const srcStaticMap = `${GOOGLE_MAP_API_URL}
        &center=${lat},${lng}
        &zoom=${zoom}
        &size=${width}x${height}
        &scale=${scale}
        &format=${format}
        &${style}`;

    return (
        <div className={cx(className, "root")}>
            <img
                src={srcStaticMap}
                alt={name}
                className={cx("image")}
                width={width}
            />
            {showBaseMarker && (
                <Icon className={cx("marker")}>
                    <PinIcon />
                </Icon>
            )}
        </div>
    );
};
