import React, { FunctionComponent, useState, useCallback } from "react";
import classnames from "classnames/bind";

import { Modal, ModalPosition } from "common/components/Modal";
import { Button, ButtonSize, ButtonTheme } from "common/components/new/Button";

import { CancelAllModalProps } from "./CancelAllModal.types";
import styles from "./CancelAllModal.module.scss";

const cx = classnames.bind(styles);

export const CancelAllModal: FunctionComponent<CancelAllModalProps> = ({
    control,
    content,
    onConfirm,
    isMobile,
    isLoading,
}) => {
    const { label, buttonConfirmationText, buttonCancelText } = content;

    const [isVisible, setVisibility] = useState(false);

    const handleOpenPopup = useCallback(() => {
        if (!isVisible) {
            document.body.style.overflow = "hidden";
            setVisibility(true);
        } else {
            document.body.style.overflow = "auto";
            setVisibility(false);
        }
    }, [isVisible]);

    const Control = () => control(handleOpenPopup);

    return (
        <>
            <Control />

            {isVisible && (
                <Modal
                    onClose={handleOpenPopup}
                    showCloseButton={false}
                    position={
                        isMobile ? ModalPosition.BOTTOM : ModalPosition.RIGHT
                    }
                >
                    <div className={cx("modal-content")}>
                        <h2 className={cx("modal-label")}>{label}</h2>
                        <div className={cx("modal-actions")}>
                            <Button
                                size={ButtonSize.M}
                                onClick={onConfirm}
                                isMaxWidth={isMobile}
                                isLoading={isLoading}
                            >
                                {buttonConfirmationText}
                            </Button>

                            <Button
                                size={ButtonSize.M}
                                isMaxWidth={isMobile}
                                onClick={handleOpenPopup}
                                theme={ButtonTheme.BORDERED_TRANSPARENT}
                            >
                                {buttonCancelText}
                            </Button>
                        </div>
                    </div>
                </Modal>
            )}
        </>
    );
};
