import { ShowUpPageBookingItem } from "common/api/contacts";

export const sortBookingsByDateAsc = (
    bookings: ShowUpPageBookingItem[]
): ShowUpPageBookingItem[] => {
    return bookings.sort((a, b) => {
        const aDate = new Date(a.startsAt);
        const bDate = new Date(b.startsAt);

        return aDate > bDate ? 1 : aDate < bDate ? -1 : 0;
    });
};
