import { FC } from "react";
import classNames from "classnames/bind";

import { MaxWidthContainer } from "common/components/new/MaxWidthContainer";
import { ShowUpMainBanner as MainBanner } from "common/components/new/ShowUpMainBanner";
import { AdvantagesGrid } from "common/components/AdvantagesGrid";
import { Container } from "common/components/Container";
import { MedicsEquipment } from "common/components/new/MedicsEquipment";

import { Footer } from "components/Footer";
import { Slider } from "components/Slider";

import { ShowUpPageProps } from "./ShowUpPageLayout.types";
import styles from "./ShowUpPageLayout.module.scss";

const cx = classNames.bind(styles);

export const ShowUpPageLayout: FC<ShowUpPageProps> = ({
    language,
    content,
    version,
}) => {
    const {
        contacts,
        mainBanner,
        medicsEquipment,
        advantages,
        slides,
    } = content;

    return (
        <MaxWidthContainer>
            <main className={cx("root")}>
                <MainBanner
                    {...mainBanner}
                    language={language}
                    version={version}
                    linkLogo={contacts.site.link}
                />
                <div className={cx("container", "first-visit")}>
                    <AdvantagesGrid {...advantages} version={version} />
                </div>
                <Slider className={cx("slider")} {...slides} />
                <Container>
                    <MedicsEquipment {...medicsEquipment} />
                    <Footer {...contacts} />
                </Container>
            </main>
        </MaxWidthContainer>
    );
};
