type GoogleCalendarProps = {
    title: string;
    startDate: string;
    endDate: string;
    details: string;
    location: string;
};

export const getCalendarUrl = ({
    title,
    startDate,
    endDate,
    details,
    location,
}: GoogleCalendarProps) => {
    const baseUrl = "https://calendar.google.com/calendar/render";
    return `${baseUrl}?action=TEMPLATE&text=${encodeURIComponent(
        title
    )}&dates=${startDate}/${endDate}&details=${details}&location=${encodeURIComponent(
        location
    )}`;
};

export const getCalendarDate = (dateNow: Date) => {
    const date = ("0" + dateNow.getDate()).slice(-2);
    const month = ("0" + (dateNow.getMonth() + 1)).slice(-2);

    return `${dateNow.getFullYear()}${month}${date}`;
};
