import { toDate, utcToZonedTime } from "date-fns-tz";

import { ISOLanguagesCodeByLanguage, Language } from "common/constants/enum";
import { ShowUpPageBookingItem } from "common/api/contacts";

import { AppointmentDetailsBooking } from "./AppointmentDetails.types";

export const normalizeBookingInfo = (
    {
        uuid,
        startsAt,
        endsAt,
        visitIntentionStatus,
        clinic,
    }: ShowUpPageBookingItem,
    language: Language
): AppointmentDetailsBooking => {
    const {
        timezone,
        address,
        cityTitle,
        latitude,
        longitude,
        title,
        googleMapClinicUrl,
    } = clinic;

    const eventStartDate = utcToZonedTime(toDate(startsAt), timezone);
    const eventEndDate = utcToZonedTime(toDate(endsAt), timezone);

    return {
        id: uuid,
        eventStartDate: eventStartDate,
        eventEndDate: eventEndDate,
        eventStartDateFormatted: new Date(eventStartDate).toLocaleDateString(
            ISOLanguagesCodeByLanguage[language],
            {
                weekday: "long",
                day: "numeric",
                month: "short",
            }
        ),
        eventStartTime: new Date(eventStartDate).toTimeString().slice(0, 5),
        status: visitIntentionStatus,
        location: {
            address,
            city: cityTitle,
            coordinates: {
                lat: Number(latitude),
                lng: Number(longitude),
            },
            clinic: {
                name: title,
                link: googleMapClinicUrl,
            },
        },
    };
};
